import React from 'react';
import { connect } from 'react-redux'
import { Button} from 'react-bootstrap'
import { Redirect } from 'react-router-dom'

import { createNote } from '../reducers/noteReducer'
import { notify, errormessage } from '../reducers/notificationReducer'

class NoteForm extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			isMounted: false,
			redirect: false,
			title: '',
			content: '',
			tag_id: ''
		}
	}
	componentDidMount() {
		this.setState( { isMounted: true } )
	}
	componentWillUnmount() {
	    this.setState( { isMounted: false } )
	}

	handleChange = (event) => {
		if(!this.state.isMounted) return
  	// console.log(event.target.name, event.target.value)
  	this.setState({ [event.target.name]: event.target.value })
  }
	handleContent = (event) => {
		this.setState({ content: event.target.value })
	}

	handleSubmit = async (event) => {
		event.preventDefault()
		try {
			const noteObject = {
				otsikko: this.state.title,
				sisalto: this.state.content,
				tagi_id: this.state.tag_id
			}
			await this.props.createNote(noteObject)
			await this.props.notify(`you created '${noteObject.otsikko}'`, 10)
			this.setState({
				title: '',
				content: '',
				tag_id: ''
			})
			await this.setState({ redirect: true })
		} catch(exception) {
			this.props.errormessage('ERROR WHILE ADDING NOTE', 10)
		}
	}

 render() {
	 if (this.state.redirect) {
		 return <div><Redirect to='/' /></div>
	 }

   return(
     <div>
       <h2>Create new note</h2>
       <form onSubmit={this.handleSubmit}>
         <div>
           Title<br />
           <input name='title' value={this.state.title} onChange={this.handleChange} />
         </div>
         <div>
          <label>
	 				 Content<br />
         	<textarea value={this.state.content} onChange={this.handleContent} rows='20' cols='60' />
       		</label>
         </div>
         <div>
           Tag_id<br />
           <input name='tag_id' value={this.state.tag_id} onChange={this.handleChange} />
         </div> 
	 			<br/>
         <Button bsStyle="success" type="submit">Create</Button>
       </form>
     </div>  
   )
 }
}

const mapDispatchToProps = {
	createNote,
	notify,
	errormessage
}

const ConnectedNoteForm = connect(
	null,
	mapDispatchToProps
)(NoteForm)

export default ConnectedNoteForm




